import React from "react";
import { useHistory } from "react-router";
import "../styles/Header.css"
import Logo from '../assets/LOGO.png'

interface HeaderProps {
  isModal: boolean
  setIsModal: React.Dispatch<React.SetStateAction<boolean>>
}

const Header: React.FunctionComponent<HeaderProps> = ({isModal, setIsModal}) => {

  let history = useHistory()

  const routerHandler: (pageId: number) => void = (pageId: number) => {
      if(pageId === 0 ) history.push('/')
      else if (pageId === 1 ) history.push('/marketplace')
      else if(pageId === 2 ) history.push('/gallery')
      else if(pageId === 3 ) history.push('/mystuff') 
  }
  return (
    <nav className="navbar">
        <div className="logo" onClick={() => routerHandler(0)}>
            <img className="navbar_logo" src={Logo} width={56} height={56} alt="logo"/>
        </div>
      
      <ul
        className="navbar__list"
      >
            <li className="navbar__item" >
              <p className="navbar__link" onClick={() => routerHandler(1)}>Marketplace</p>
            </li>
            <li className="navbar__item" >
              <p className="navbar__link" onClick={() => routerHandler(2)}>Gallery</p>
            </li>
            <li className="navbar__item" >
              <p className="navbar__link" onClick={() => routerHandler(3)}>My stuff</p>
            </li>
      </ul>
      <button 
        onClick={() => setIsModal(!isModal)}
        className="walletButton"
      >Connect Wallet</button>
    </nav>
  );
};

export default Header;
